.search-box {
    width: 100%;
    text-align: center;
}

.search-bar {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 800px;
    padding: 10px;
    font-size: 20px;
    margin: 0 auto 20px auto;
    border: none;
    background: none;
    outline: none;
    background-color: rgba(255, 255, 255, 0.75);
    border-radius: 16px 0 16px 0;
    border-bottom: 3px solid rgba(0, 0, 0, 0.6);
    transition: 0.3s ease-out;
}

.search-bar:focus {
    background-color: rgba(255, 255, 255, 0.9);
}

.search-box button {
    padding: 10px 20px;
    font-size: 25px;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 3px 3px solid rgba(0, 0, 0, 0.6);
    border: none;
    border-radius: 1rem;
    cursor: pointer;
}