.additional-info {
    margin: 87px auto 0 auto;
    width: 100%;
    max-width: 580px;
    margin-top: 105px;
    text-align: left;
}

.intro {
    color: white;
    font-weight: 100;
    font-size: 20px;
    position: relative;
    width: 100%;
    border-bottom: 25px;
    margin-left: auto;
    margin-right: auto;
}

.blocks {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 5px;
    margin-left: auto;
    margin-right: auto;
}

.info {
    width: 120px;
    height: 120px;
    margin-top: 10px;
    background-color: rgba(255, 255, 255, 0.15);
    border-radius: 5px;
    color: white;
    font-weight: 100;
    text-align: center;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.stat {
    margin-bottom: 5px;
}

.icon {
    font-size: 30px;
    margin-bottom: 5px;
}

.info span {
    font-weight: bold;
}