.weather-box {
    color: white;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
    width: 280px;
    height: 200px;
    text-align: center;
    margin-top: 15px;
    font-size: 36px;
    margin-left: auto;
    margin-right: auto;
}

.date {
    font-size: 20px;
}

.status {
    font-size: 24px;
}

.location {
    margin-top: 10px;
}

.temp {
    padding: 20px 10px;
    border-radius: 1rem;
    height: 220px;
    font-size: 136px;
    font-weight: 100;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -20px;
}

.min-max {
    font-size: 24px;
}

.max {
    position: relative;
    padding-bottom: 8px;
}

.min {
    padding-top: 8px;
}

.max::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;

    height: 1px;
    background-color: rgba(255, 255, 255, 0.6);
    width: 100%;
}